import {Component, EventEmitter, OnInit, Output, TemplateRef} from '@angular/core';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import * as $ from 'jquery';
import {HttpService} from '../../../service/http.service';
import {Teacher} from './Teacher';
import {Student} from '../student/Student';

@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {
  modalRef: BsModalRef;
  config: any = {
    animated: true,
    backdrop: 'static',
    keyboard: false
  };
  @Output() outer = new EventEmitter();
  teachers: Array<Teacher>;
  checkTeacher: Array<Teacher> = new Array<Teacher>();

  constructor(private modalService: BsModalService, private http: HttpService) {
  }

  ngOnInit() {
    this.http.showTeachers().subscribe((res: any) => {
      this.teachers = res;
    });
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, this.config);
  }

  decline(): void {
    this.modalRef.hide();
  }

  confirm(e: any): void {
    this.checkTeacher.length = 0;
    for (let i = 0; i < $('.check_item:checked').length; i++) {
      const teacher = new Teacher();
      teacher.tid = $('.check_item:checked').eq(i).attr('id');
      teacher.tname = $('.check_item:checked').eq(i).attr('name');
      this.checkTeacher.push(teacher);
    }
    this.outer.emit(this.checkTeacher);
    this.decline();
  }

  checkAll() {
    $('.check_item').prop('checked', $('#check_all').prop('checked'));
  }

  checkItem(e: any) {
    e.stopPropagation();
    const flag = $('.check_item:checked').length === $('.check_item').length;
    $('#check_all').prop('checked', flag);
  }

  checkLine(e: any) {
    e.target.parentNode.firstChild.firstChild.checked = !e.target.parentNode.firstChild.firstChild.checked;
  }

  remove(checkTeacher: Teacher) {
    this.checkTeacher = this.checkTeacher.filter((teacher: Teacher) => {
        return teacher !== checkTeacher;
      }
    );
  }
}
