import {AfterViewInit, Component, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {ActivatedRoute, Params, Router} from '@angular/router';
import * as $ from 'jquery';
import {AuthService} from '../service/auth.service';
import {User} from '../home/user';
import {FileService} from '../service/file.service';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {CookieService} from 'ngx-cookie-service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  user: User = new User();
  users: Array<User>;
  today: Date;
  config: any = {
    animated: true,
    backdrop: 'static',
    keyboard: false
  };
  role = '答辩秘书';

  constructor(private router: Router, private route: ActivatedRoute,
              private fileService: FileService, private cookieService: CookieService) {
    this.user.userName = this.cookieService.get('userName');
    setInterval(() => {
      this.today = new Date();
    }, 50);
  }

  ngOnInit() {
    // 接收路由参数
    this.route.params.subscribe((params: Params) => {
      this.user.uid = params['uid'];
    });
    // 去除所有class属性，并为选中添加class属性，标记选中状态
    $('.menu-item').click(function () {
      $('.menu-item').removeClass('menu-item-active');
      $(this).addClass('menu-item-active');
      $('#report').hide();
      $('#role').hide();
      // $('.toast-warning.ngx-toastr.ng-trigger.ng-trigger-flyInOut').hide();
    });
    $('.manage').hide();
  }

  reply() {
    this.role = '答辩秘书';
    $('.reply').show();
    $('.manage').hide();
  }

  manage() {
    this.role = '管理秘书';
    $('.manage').show();
    $('.reply').hide();
  }

  report() {
    this.reply();
    $('#group').click();
    $('#report').hide();
  }

  decline() {
    $('#report').hide();
  }

  // 登出，清除信息并跳转至初始页面
  logout() {
    this.cookieService.deleteAll();
    this.router.navigate(['/home']);
  }

  showFiles() {
    // 展示所有文件
    this.fileService.showFiles().subscribe((res: any[]) => {
        // res.forEach((user) => {
        //   // 只显示自己上传的文件
        //   if (user.uid === this.user.uid) {
        //     this.user = user;
        //   }
        // });
        this.users = res;
      }
    );
  }
}

